import axios from 'axios'
import store from '../../store'
import router from '../../router'
var url = 'https://reqapi.kmphimgiare.com/api';
// var url = 'http://127.0.0.1:8000/api';
const state = {
    list: [],
    curPage: 1,
    lastPage: 1,
    totalAcc: 1,
}
const getters = {

}
const actions = {
    async storeAccount({ commit }, payload) {
        try {
            let token = window.localStorage.getItem('token');
            if (token === null)
                router.push({ path: 'login-admin-root' });
            const response = await axios.post(url + '/account', payload, { headers: { Authorization: 'Bearer ' + token } });
            commit('ALERT', ['Success', 1]);
            console.log(response.data);
        } catch (err) {

            console.log('err login: ' + err)
        }
    },
    async getAccounts({ commit }, payload) {
        try {
            let token = window.localStorage.getItem('token');
            if (token === null)
                router.push({ path: 'login-admin-root' });
            const response = await axios.get(url + '/account?page= ' + payload, { headers: { Authorization: 'Bearer ' + token } });
            commit('SET_ACCOUNT', response.data.data);
            commit('SET_PAGE', [response.data.current_page, response.data.last_page, response.data.total]);

        } catch (err) {

            console.log('err login: ' + err)
        }
    },
    async reuseAccount({ commit }, payload) {
        try {
            let token = window.localStorage.getItem('token');
            if (token === null)
                router.push({ path: 'login-admin-root' });
            await axios.put(url + '/account/' + payload[0], payload, { headers: { Authorization: 'Bearer ' + token } });
            commit('ALERT', ['Success', 1]);
            store.dispatch('getTokens');
        } catch (err) {
            console.log('err login: ' + err)
        }
    },
    async removeAcc({ commit }, payload) {
        try {
            let token = window.localStorage.getItem('token');
            if (token === null)
                router.push({ path: 'login-admin-root' });
            await axios.delete(url + '/account/' + payload, { headers: { Authorization: 'Bearer ' + token } });
            commit('ALERT', ['Success', 1]);
            store.dispatch('getAccounts');
        } catch (err) {
            console.log('err login: ' + err)
        }
    },
    async removeAccount({ commit }, payload) {
        try {
            let token = window.localStorage.getItem('token');
            if (token === null)
                router.push({ path: 'login-admin-root' });
            await axios.post(url + '/remove-account-token', payload, { headers: { Authorization: 'Bearer ' + token } });
            commit('ALERT', ['Success', 1]);
            store.dispatch('getTokens');
        } catch (err) {
            console.log('err login: ' + err)
        }
    },
}
const mutations = {
    SET_ACCOUNT(state, data) {
        state.list = data
    },
    SET_PAGE(state, data) {
        state.curPage = data[0];
        state.lastPage = data[1];
        state.totalAcc = data[2];
    },

}
export default {
    state,
    getters,
    actions,
    mutations
}