import axios from 'axios'
import store from '../../store'
import router from '../../router'
var url = 'https://reqapi.kmphimgiare.com/api';
// var url = 'http://127.0.0.1:8000/api';
const state = {
    list: [],
    curPage: 1,
    lastPage: 1
}
const getters = {
    getListToken() {
        console.log('getter');
        return store.state.token;
    }
}
const actions = {
    async getTokens({ commit }, payload) {
        try {
            let token = window.localStorage.getItem('token');
            if (token === null)
                router.push({ path: 'login-admin-root' });
            const response = await axios.get(url + '/token?page=' + payload, { headers: { Authorization: 'Bearer ' + token } });
            commit('GET_TOKENS', response.data.data);
            commit('GET_PAGE_MANAGER', [response.data.current_page, response.data.last_page]);
            console.log(response.data);
        } catch (err) {

            console.log('err login: ' + err)
        }
    },
    async storeToken({ commit }, payload) {
        try {
            // check localStorage
            let token = window.localStorage.getItem('token');
            if (token === null)
                router.push({ path: 'login-admin-root' });
            const response = await axios.post(url + '/token', payload, { headers: { Authorization: 'Bearer ' + token } });
            commit('ALERT', [' lu token', 1]);
            console.log(response.data);
        } catch (err) {

            console.log('err login: ' + err)
        }
    },
    async updateToken({ commit }, payload) {
        try {
            // check localStorage
            let token = window.localStorage.getItem('token');
            if (token === null)
                router.push({ path: 'login-admin-root' });
            await axios.put(url + '/token/' + payload[0], payload[1], { headers: { Authorization: 'Bearer ' + token } });
            commit('ALERT', ['Success', 1]);
            store.dispatch('getTokens');
        } catch (err) {

            console.log('err login: ' + err)
        }
    },
    async banToken({ commit }, payload) {
        try {
            // check localStorage
            let token = window.localStorage.getItem('token');
            if (token === null)
                router.push({ path: 'login-admin-root' });
            await axios.post(url + '/ban-token', payload, {
                headers: {
                    Authorization: 'Bearer ' + token,
                    'Access-Control-Allow-Origin': '*',
                    'Access-Control-Allow-Methods': 'DELETE, POST, GET, OPTIONS',
                    'Access-Control-Allow-Headers': 'Content-Type, Access-Control-Allow-Headers, Authorization, X-Requested-With'
                }
            });
            commit('ALERT', ['Success', 1]);


        } catch (err) {
            console.log('err login: ' + err)
        }
    },
    async deletePivotToken({ commit }, payload) {
        try {
            // check localStorage
            let token = window.localStorage.getItem('token');
            if (token === null)
                router.push({ path: 'login-admin-root' });
            await axios.delete(url + '/token/' + payload, { headers: { Authorization: 'Bearer ' + token } });
            commit('ALERT', ['Success', 1]);

        } catch (err) {
            console.log('err login: ' + err)
        }
    },
    async addAccountForToken({ commit }, payload) {
        try {
            // check localStorage
            let token = window.localStorage.getItem('token');
            if (token === null)
                router.push({ path: 'login-admin-root' });
            const response = await axios.post(url + '/add-account-token', payload, {
                headers: {
                    Authorization: 'Bearer ' + token,
                    'Access-Control-Allow-Origin': '*',
                    'Access-Control-Allow-Methods': 'DELETE, POST, GET, OPTIONS',
                    'Access-Control-Allow-Headers': 'Content-Type, Access-Control-Allow-Headers, Authorization, X-Requested-With'
                }
            });
            if (response.data.error == 464) {
                commit('ALERT', ['Khng tm thy id', 0]);
                return;
            }
            if (response.data.error == 465) {
                commit('ALERT', ['Account ang c s dng', 0]);
                return;
            }
            if (response.data.error == 466) {
                commit('ALERT', ['Account khng hp l', 0]);
                return;
            }
            commit('ALERT', ['Success', 1]);
            store.dispatch('getTokens');



        } catch (err) {
            console.log('err login: ' + err)
        }
    },
}
const mutations = {
    GET_TOKENS(state, data) {
        state.list = data;
    },
    GET_PAGE_MANAGER(state, data) {
        state.curPage = data[0];
        state.lastPage = data[1];
    },
    UPDATE_STATE_BAN(state, data) {
        if (data[0].status === 200) {
            let tokenIndex = state.list.findIndex(tokenIndex => tokenIndex.id === data[1]);
            if (state.list[tokenIndex].status == 0)
                state.list[tokenIndex].status = 1;
            else
                state.list[tokenIndex].status = 0;
        }
    }
}
export default {
    state,
    getters,
    actions,
    mutations
}