import axios from 'axios'
var url = 'https://reqapi.kmphimgiare.com/api';
// var url = 'http://127.0.0.1:8000/api';
const state = {
    username: '',
    password: '',
    time: '',
    qualify: 0,
}
const getters = {

}
const actions = {
    async getAccount({ commit }, payload) {
        try {
            // check localStorage
            const response = await axios.post(url + '/get-account', payload);
            if (response.data == 460) {
                commit('ALERT', ['Token tm thi b kha', 0]);
                commit('HAVING_ERROR');
                return;
            }
            if (response.data.error == 461) {
                commit('ALERT', [' ly qu nhiu, vui lng quay li hm sau', 0]);
                commit('HAVING_ERROR');
                return;
            }
            if (response.data.error == 462) {
                commit('ALERT', ['Token khng chnh xc', 0]);
                commit('HAVING_ERROR');
                return;
            }
            if (response.data.error == 463) {
                commit('ALERT', ['Token  ht hn', 0]);
                commit('HAVING_ERROR');
                return;
            }
            commit('GET_ACC_CLIENT', response.data);
        } catch (err) {
            console.log('err login: ' + err)
        }
    },
    async getNewAccount({ commit }, payload) {
        try {
            // check localStorage
            const response = await axios.post(url + '/get-account-new', payload);

            if (response.data.error == 460) {
                commit('ALERT', ['Token tm thi b kha', 0]);
                commit('HAVING_ERROR');
                commit('GET_ACC_CLIENT');
                return;
            }
            if (response.data.error == 464) {
                commit('ALERT', ['Token ny  b v hiu ha', 0]);
                commit('HAVING_ERROR');
                return;
            }
            if (response.data.error == 461) {
                commit('ALERT', [' ly qu nhiu, vui lng quay li hm sau', 0]);
                commit('HAVING_ERROR');

                return;
            }
            if (response.data.error == 462) {
                commit('ALERT', ['Token khng chnh xc', 0]);
                commit('HAVING_ERROR');

                return;
            }
            if (response.data.error == 463) {
                commit('ALERT', ['Token  ht hn', 0]);
                commit('HAVING_ERROR');
                return;
            }
            if (response.data.error == 465) {
                commit('ALERT', ['Vui lng quay li sau t pht', 0]);
                commit('HAVING_ERROR');
                return;
            }
            commit('GET_ACC_CLIENT', response.data);
            commit('ALERT', ['Nhn ti khon mi thnh cng', 1]);
        } catch (err) {
            console.log('err login: ' + err)
        }
    },
}
const mutations = {
    GET_ACC_CLIENT(state, data) {
        state.username = data.username;
        state.password = data.password;
        state.time = data.time;
        state.qualify = data.qualify;
    }
}
export default {
    state,
    getters,
    actions,
    mutations
}